.novelty-item {
  position: relative;
  display: block;
  text-align: center;
  font-size: 18px;
  background: #fff;
  transition: .3s ease;
}

.novelty-item::after {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 0;
  background: #fff;
  transition: .3s ease;
  content: '';
}

.novelty-item:hover::after {
  transform: scale(1.05);
  box-shadow: 0 0 15px rgba(19, 19, 19, 0.3);
}

.novelty-item__inner {
  position: relative;
